body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

nav {
    margin: 0 auto;
    border-bottom: 1px #eee solid;
}

.container {
    max-width: 800px;
    position: relative;
    /* padding-bottom: 15px; */
} 

.nav-item {
    float: right;
    padding: 5px;
    text-align: center;
    list-style: none;
    width: 75px;
    border-left: 1px #eee solid;
    border-bottom: 1px #eee solid;
    margin-right: 15px;
    color: black;
    margin-top: 10px;
}

.nav-item-left {
    float: left;
    padding: 5px;
    text-align: center;
    list-style: none;
    width: 40px;
}

.nav-name {
    font-family: 'Orbitron', sans-serif;
    width: 125px;
    font-size: 1.5em;
    margin-right: 15px;
}

.nav-icons {
    width: 40px;
    height: 40px;
}

.nav-icons-linkedin {
    width: 30x;
    height: 30px;
    padding-top: 5px;
}

.nav-half-left {
    padding: 0 0 0 40px ;
    height: 40px;

}

.nav-half-right {
    padding: 0 20px 0 0 ;
    height: 55px;

}

.logo-link {
    color: black;
}

.logo-link:hover {
    color: black;
}
.main {
    max-width: 800px;
    margin: 0 auto;
    margin-top: 25px;
    height: 100%;
    border: 1px #eee solid;
    padding: 15px;
    width: 90%;
    margin-bottom: 80px;
}

h1 {
    font-family: 'Orbitron', sans-serif;
}
footer {
    text-align: center;
    border-top: 1px #eee solid;
    padding: 15px;
    position: fixed;
    bottom: 0;
    width: 100%;
    background-color: white;
}
img.headshot {
    max-width: 50%;
    height: auto;
    float: right;
    margin-top: 15px;
    margin-left: 20px;
}
.project-card {
    display: inline-block;
    padding: 10px;
}
img.port-image-div {
    padding: 0;
    width: 100%;
    border: 1px black solid;
}
div {
    padding:0px;
}
.resume-area {
    text-align: center;
    padding-top: 15px;
}

.resume-button {
    margin-top: 10px;
}

.project-title {
    font-family: 'Orbitron', sans-serif;
    font-size: 1.2em;
}

ul {
    list-style: none;
}

hr.mini-projects {
    margin: 15px 0 15px 0
}
